import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
  Box,
  Container,
  TextField,
  IconButton,
  Typography,
  Paper,
  Avatar,
  CircularProgress,
  Stack,
  useTheme,
  AppBar,
  Toolbar,
  Tooltip,
  Snackbar,
  Alert,
  useMediaQuery,
} from '@mui/material';
import {
  Send as SendIcon,
  SmartToy as BotIcon,
  Person as PersonIcon,
  MoreVert as MoreIcon,
} from '@mui/icons-material';

const API_BASE_URL = 'https://phosai-dbec65d5-46be-45ae-9062.cranecloud.io';
const DEFAULT_BOT_NAME = 'My Resume';

const MessageBubble = ({ message, theme }) => {
  const isUser = message.role === 'user';
  
  const formatTimestamp = (timestamp) => {
    return new Intl.DateTimeFormat('en-US', {
      hour: 'numeric',
      minute: 'numeric',
      hour12: true
    }).format(new Date(timestamp));
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: isUser ? 'flex-end' : 'flex-start',
        mb: 2,
      }}
    >
      {!isUser && (
        <Avatar
          sx={{
            bgcolor: theme.palette.primary.main,
            mr: 1,
            width: 32,
            height: 32,
          }}
        >
          <BotIcon sx={{ fontSize: 20 }} />
        </Avatar>
      )}
      
      <Paper
        elevation={1}
        sx={{
          p: 2,
          maxWidth: '70%',
          borderRadius: 2,
          bgcolor: isUser ? 'primary.main' : 'background.paper',
          color: isUser ? 'common.white' : 'text.primary',
        }}
      >
        <Typography variant="body1" sx={{ wordBreak: 'break-word' }}>
          {message.content}
        </Typography>
        <Typography
          variant="caption"
          sx={{
            display: 'block',
            mt: 1,
            color: isUser ? 'rgba(255,255,255,0.7)' : 'text.secondary',
          }}
        >
          {formatTimestamp(message.timestamp)}
        </Typography>
      </Paper>

      {isUser && (
        <Avatar
          sx={{
            bgcolor: 'grey.400',
            ml: 1,
            width: 32,
            height: 32,
          }}
        >
          <PersonIcon sx={{ fontSize: 20 }} />
        </Avatar>
      )}
    </Box>
  );
};

const Chatbot = ({ 
  agentId, 
  targetLang = 'en',
  botName = DEFAULT_BOT_NAME 
}) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setLoading] = useState(false);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [isTyping, setIsTyping] = useState(false);
  const [streamingText, setStreamingText] = useState('');
  const [showWelcome, setShowWelcome] = useState(true);

  const chatContainerRef = useRef(null);
  const messagesEndRef = useRef(null);

  const scrollToBottom = useCallback(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const streamResponse = async (response) => {
    setIsTyping(true);
    let streamedText = '';
    const words = response.split(' ');
    
    for (let word of words) {
      streamedText += word + ' ';
      setStreamingText(streamedText);
      await new Promise(resolve => setTimeout(resolve, Math.random() * 50 + 30));
    }
    
    setIsTyping(false);
    setStreamingText('');
    return streamedText.trim();
  };

  const showSnackbar = (message, severity = 'success') => {
    setSnackbar({ open: true, message, severity });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    setShowWelcome(false); // Hide welcome message on first interaction

    const userMessage = {
      role: 'user',
      content: input.trim(),
      timestamp: new Date().toISOString()
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setLoading(true);

    try {
      const formData = new FormData();
      formData.append('agent_id', agentId);
      formData.append('query', userMessage.content);
      formData.append('target_lang', targetLang);

      const response = await fetch(`${API_BASE_URL}/agents/conversations`, {
        method: 'POST',
        body: formData,
        headers: {
          'Accept': 'application/json',
        },
        credentials: 'include',
      });

      if (!response.ok) {
        throw new Error(`Server error: ${response.status}`);
      }

      const data = await response.json();
      
      if (!data || !data.answer) {
        throw new Error('Invalid response from server');
      }

      const streamedResponse = await streamResponse(data.answer);
      const botMessage = {
        role: 'assistant',
        content: streamedResponse,
        timestamp: new Date().toISOString()
      };

      setMessages(prev => [...prev, botMessage]);
    } catch (error) {
      console.error('Error:', error);
      showSnackbar(error.message || 'Error processing message. Please try again.', 'error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        height: '100vh', // Set to full viewport height
        display: 'flex',
        flexDirection: 'column',
        bgcolor: 'grey.50',
      }}
    >
      <AppBar position="static" elevation={2}>
        <Toolbar sx={{ minHeight: 64 }}>
          <Avatar 
            sx={{ 
              bgcolor: 'primary.dark',
              mr: 2,
              width: 40,
              height: 40,
              border: '2px solid rgba(255, 255, 255, 0.2)',
            }}
          >
            <BotIcon sx={{ fontSize: 24 }} />
          </Avatar>
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant="h6" sx={{ fontWeight: 600 }}>
              {botName}
            </Typography>
            <Typography variant="caption" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
              AI Powered Assistant
            </Typography>
          </Box>
          <Tooltip title="Options">
            <IconButton color="inherit">
              <MoreIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
      </AppBar>

      <Box
        ref={chatContainerRef}
        sx={{
          flex: 1,
          overflowY: 'auto',
          px: 2,
          py: 3,
          '&::-webkit-scrollbar': {
            width: '8px',
          },
          '&::-webkit-scrollbar-track': {
            background: 'transparent',
          },
          '&::-webkit-scrollbar-thumb': {
            background: theme.palette.grey[300],
            borderRadius: '4px',
            '&:hover': {
              background: theme.palette.grey[400],
            },
          },
        }}
      >
        <Container maxWidth="md">
          {showWelcome && messages.length === 0 ? (
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                minHeight: '50vh',
                textAlign: 'center',
                color: 'text.secondary',
              }}
            >
              <Avatar
                sx={{
                  width: 80,
                  height: 80,
                  bgcolor: 'primary.light',
                  mb: 2,
                }}
              >
                <BotIcon sx={{ fontSize: 40 }} />
              </Avatar>
              <Typography variant="h6" gutterBottom>
                Welcome to {botName}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Start a conversation by typing a message below
              </Typography>
            </Box>
          ) : (
            <Stack spacing={2}>
              {messages.map((message, index) => (
                <MessageBubble key={index} message={message} theme={theme} />
              ))}
              {isTyping && (
                <MessageBubble
                  message={{
                    role: 'assistant',
                    content: streamingText || '...',
                    timestamp: new Date().toISOString(),
                  }}
                  theme={theme}
                />
              )}
            </Stack>
          )}
          <div ref={messagesEndRef} />
        </Container>
      </Box>

      <Paper
        component="form"
        onSubmit={handleSubmit}
        elevation={3}
        sx={{
          p: 2,
          bgcolor: 'background.paper',
          borderTop: '1px solid',
          borderColor: 'divider',
        }}
      >
        <Container maxWidth="md">
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            <TextField
              fullWidth
              variant="outlined"
              placeholder="Type your message..."
              value={input}
              onChange={(e) => setInput(e.target.value)}
              disabled={isLoading}
              size="small"
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 2,
                }
              }}
            />
            <IconButton
              type="submit"
              disabled={!input.trim() || isLoading}
              sx={{
                bgcolor: input.trim() ? 'primary.main' : 'grey.300',
                color: 'white',
                '&:hover': {
                  bgcolor: input.trim() ? 'primary.dark' : 'grey.400',
                },
                transition: 'all 0.2s',
                width: 40,
                height: 40,
              }}
            >
              {isLoading ? (
                <CircularProgress size={24} color="inherit" />
              ) : (
                <SendIcon />
              )}
            </IconButton>
          </Box>
        </Container>
      </Paper>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      >
        <Alert
          severity={snackbar.severity}
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Chatbot;